/* Collection list page */
.page-template.page-list-collections {
    margin-bottom: 60px; 
    margin-top: 0;
    .section-list-collection {
        .section-header {
            margin-bottom: 24px;  
        }
        .page-title {
            font-size: calc(var(--body-font-size) + 4px);
            text-transform: uppercase;
        }
    } 
    .collection-card {
        background: var(--second-bg-color);
        padding: var(--gutter-xs);
        .collection-card_image {
            margin-bottom: 15px;
        }
        .collection-card_title {
            @media (min-width: 750px) {
                font-size: calc(var(--body-font-size) + 2px);
            }
        }
        &:hover {
            @media (hover: hover) {
                box-shadow: 0px 5px 24px rgba(35, 35, 35, 0.18);  
            } 
        }
    }  
} 
/* End Collection list page */

/* Collection page */
.page-template.page-collection-template {
    margin-bottom: 60px; 
    margin-top: 0;  
    .collection-template_title {
        text-transform: uppercase; 
        margin-bottom: 5px;
    }
    .collection-template_description {
        color: var(--body-color-2);
    }
    .collection-template .section-header {
        border-bottom: 1px solid var(--body-border-color);
        margin-bottom: 23px; 
        padding-bottom: 20px;
    } 
}
.collection-filters {
    background: var(--second-bg-color);    
    padding: 23px 24px; 
    margin-bottom: 30px;
    overflow-x: hidden;
    overflow-y: auto;
    display: block;
    @media (min-width: 992px) {
        position: sticky;
        top: calc(var(--header-height) + 20px);
    } 
    @media (min-width: 1000px) {
        margin-bottom: 0; 
    }
    @include custom-scrollbar; 
    .filter-group_wrapper {
        display: flex;
        @include margin-start(5px); 
    } 
    .svg-loading {
        display: none;
        width: 32px;
        height: 20px;
    }   
    .filter-group[pending] { 
        .bold-angle-down {
            display: none;
        }
        .svg-loading {
            display: block;
        }
    }
    .collection-filters_title {
        font-size: calc(var(--body-font-size) + 4px);
        @include font-heading;
        font-weight: var(--font-bold);
        margin-bottom: 15px;
    }
    .collection-filters_form .filter-group:not(:last-child) {
        border-bottom: 1px solid var(--body-border-color);
        margin-bottom: 19px;
        padding-bottom: 18px; 
    }
    .filter-group_title {
        @include list-style-reset;
        display: flex;
        align-items: center;
        justify-content: space-between; 
        font-size: calc(var(--body-font-size) + 2px);
        text-transform: uppercase;
        @include point-cursor;
        @include font-heading;
    } 
    .filter-group_inner {
        margin-top: 10px;  
        max-height: 320px;
        overflow-y: auto;
        overflow-x: hidden; 
        @include custom-scrollbar;
        .filter-group_item:not(:last-child) { 
            margin-bottom: 11px;    
        }
    }
    .filter-group_item {
        position: relative; 
        input {
            @include ab(0, 0, n, n);
            width: 100%; 
            height: 100%;
            z-index: -1;
            border: none;
            padding: 0;
            background: transparent;
            box-shadow: none; 
            &:checked ~ label { 
                &:before {
                    @include checked-background;
                }
                &:after {
                    @include checked-icon;
                    @include ab(4px, 2px, n, n);   
                } 
            } 
        }
        label {
            position: relative;
            display: flex;
            align-items: flex-start;
            margin: 0;  
            color: var(--body-link); 
            @include point-cursor;
            &:before {
                @include form-check-before;
                margin-top: 1px; 
            }
        }
        .count {
            color: var(--text-miscellaneous); 
            @include margin-start(8px);
        }
    }
}
.collection-tool-bar {
    margin-bottom: 23px; 
    flex-wrap: wrap; 
    .collection-sorting {
        flex: 1;
        justify-content: flex-end; 
        @include margin-start(15px);
    } 
    label {
        margin: 0;
        @include margin-end(12px);  
        display: none;
        @media (min-width: 750px) {
            display: block;
        }
    }
    .select-group {
        max-width: 162px;
        width: 100%;
        position: relative; 
        &:after {
            content: none;
        }
        select {
            background: transparent;
            box-shadow: none; 
            width: 100%;  
            padding: 1px 15px; 
            border: 1px solid var(--body-border-color);  
            border-radius: var(--input-radius); 
        }
        svg {
            pointer-events: none; 
            @include ab(50%, n, n, 5px); 
            transform: translate(0, -50%); 
        }
    }
}
/* End collection page */